import React from "react";
import classes from './Navbar.module.css';
import {NavLink} from "react-router-dom";
import HeaderContainer from "../Header/HeaderContainer";

const Navbar = () => {
    return (
        <div className={classes.nav__bar}>
            <HeaderContainer/>
            <nav className={classes.nav}>
                <div>
                    <NavLink to='/profile' className={classes.navLink}
                             activeClassName={classes.active}>Profile</NavLink>
                </div>
                <div>
                    <NavLink to='/dialogs' className={classes.navLink}
                             activeClassName={classes.active}>Message</NavLink>
                </div>
                <div>
                    <NavLink to='/dialogs' className={classes.navLink}>News</NavLink>
                </div>
                <div>
                    <NavLink to='/dialogs' className={classes.navLink}>Music</NavLink>
                </div>
                <div>
                    <NavLink to='/dialogs' className={classes.navLink}>Settings</NavLink>
                </div>
                <div>
                    <NavLink to='/users' className={classes.navLink}>Users</NavLink>
                </div>
            </nav>
        </div>

    )
}

export default Navbar;