import React from 'react';
import Paginator from "../command/Paginator";
import User from "./User";



let Users = (props) => {
    return (
        <div>
            <Paginator currentPage={props.currentPage} totalUsersCount={props.totalUsersCount} pageSize={props.pageSize} onPageChanged={props.onPageChanged}  />
            {
                props.users.map(u => <User user={u} unfollow={props.unfollow} follow={props.follow} followingInProgress={props.followingInProgress} key={u.id} />)}
        </div>
    )
}

export default Users;