import React from 'react';
import {NavLink} from "react-router-dom";
import classes from "./Userc.module.css";
import userPhoto from "../../assets/images/292-2924858_user-icon-business-man-flat-png-transparent-png.jpg";


let User = ({user,followingInProgress,unfollow,follow}) => {
    return (
        <div>
                <span>
                    <div>
                        <NavLink to={'/profile/' + user.id}>
                        <img src={user.small != null ? user.small : userPhoto} className={classes.usersPhoto}/>
                        </NavLink>
                    </div>
                    <div>
                        {user.followed
                            ? <button disabled={followingInProgress.some(id => id == user.id)} onClick={() => {
                                unfollow(user.id)
                            }}>Unfollow</button>
                            : <button disabled={followingInProgress.some(id => id == user.id)} onClick={() => {
                                follow(user.id)
                            }}>Follow</button>}
                            </div>
                            </span>
            <div>
                <div>
                    <div>{user.name}</div>
                    <div>{user.status}</div>
                </div>
                <span>
                            <div>{"user.location.country"}</div>
                            <div>{"user.location.city"}</div>
                            </span>
            </div>
        </div>)
}

export default User;