import React from 'react';
import classes from './MyPosts.module.css';
import Post from "./Post/Post";
import {Field, reduxForm} from "redux-form";
import {maxLenghtCreator, requiredFild} from "../../../Utils/Validators/Validator";
import {Textarea} from "../../command/FormsControls/FormsControls";


const MyPosts = React.memo(props => {
    let postsElements =
        props.posts.map((posts) =>
            <Post message={posts.message} id={posts.id}/>);


    let onAddPost = (values) => {
        props.addPost(values.newPostText)
    }
    return (
        <div className={classes.post_block}>
            <div className={classes.post_block_item}>
                <h1 className={classes.post_block_title}>My posts</h1>
                <MypostFormRedux onSubmit={onAddPost}/>
            </div>
            <div className={classes.post}>
                {postsElements}
            </div>
        </div>
    )
});

const maxLenght10 = maxLenghtCreator(10);

let MypostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className={classes.post_item_area}>
            <Field validate={[requiredFild,maxLenght10]}  component={Textarea} name="newPostText" placeholder="Enter your massege" />
            <button  className={classes.post_item_button}>Add new post
            </button>
        </form>
    )
}

let MypostFormRedux = reduxForm({form:'addPost'})(MypostForm)


export default MyPosts;