import React from 'react';
import {connect} from "react-redux";
import {
    setCurrentPage,
    toggleFollowingProgress, getUsersT, follow, unfollow,
} from "../../redux/users-reducer,js";
import Users from "./Users";
import Preloader from "../command/preloader/Preloader";
import {
    getCurrentPage,
    getFollowingInProgress,
    getIsFetching,
    getPageSize,
    getTotalUsersCount,
    getUsers, getUsersSuper
} from "../../redux/userSelector";


class UsersContainer extends React.Component {
    componentDidMount() {
        this.props.getUsersT(this.props.currentPage, this.props.pageSize);
    }

    onPageChanged = (pageNumber) => {
        this.props.getUsersT(pageNumber, this.props.pageSize);
    }

    render() {

        return (
            <>
                {this.props.isFetching ? <Preloader /> : null}
                <Users totalUsersCount={this.props.totalUsersCount}
                       pageSize={this.props.pageSize}
                       currentPage={this.props.currentPage}
                       onPageChanged={this.onPageChanged}
                       users={this.props.users}
                       follow={this.props.follow}
                       unfollow={this.props.unfollow}
                       followingInProgress={this.props.followingInProgress}
                />
            </>
        )
    }
}


