import React from "react";
import {Field, reduxForm} from "redux-form";
import {Input} from "../command/FormsControls/FormsControls";
import {maxLenghtCreator, requiredFild} from "../../Utils/Validators/Validator";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";
import classes from "../command/FormsControls/FormControls.module.css"





const maxLenght10 = maxLenghtCreator(10);

const LoginForm = ({handleSubmit,error}) => {
    return (
            <form onSubmit={handleSubmit}>
                <div>
                    <Field placeholder={'email'} name={'email'}
                           //validate={[requiredFild,maxLenght10]}
                           component={Input}/>
                </div>
                <div>
                    <Field placeholder={'Password'} name={'password'} component={Input}/>
                </div>
                <div>
                    <Field type={'checkbox'} name={'rememberMe'} component={Input}/>
                </div>
                { error && <div className={classes.group}>
                    {error}
                </div>}
                <div>
                    <button> Login</button>
                </div>
            </form>
    )
}

const ReduxLoginForm = reduxForm(
    {
        form: 'login'
    }
) (LoginForm)


const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe);
    }

    if(props.isAuth) {
        return <Redirect to={'/profile'}/>
    }
    return (
        <div>
            <ReduxLoginForm onSubmit={onSubmit}/>
        </div>
    )
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login} )(Login);